body {
  margin: 1rem;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

pre {
  white-space: pre-wrap;   
  word-wrap: break-word;
}

.app {
  max-width: 960px;
  margin: 1rem auto;
  padding: 1rem;
}

.heading h1 {
  margin: 0 0 0.25rem 0;
}

.heading h5 {
  margin: 0 0 2rem 0;
  color: #666;
}

.control {
  display: inline-block;
  margin-right: 2rem; 
}

.control input {
  width: 3rem;
}
.control select {
  width: 6rem;
}

.control label {
  padding-right: 0.5rem; 
}

.output {
  border: 1px solid #ccc;
  background: #eee;
  padding: 1rem 2rem;
  margin: 1rem auto;
}
 


